@import url("https://fonts.googleapis.com/css2?family=Rajdhani&family=Source+Code+Pro&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inconsolata:wght@300&display=swap");

body {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 0;
  height: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  padding-top: 30px;
  font-family: "Rajdhani", sans-serif;
  font-size: 18px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  background-color: #000000;
  background-image: -webkit-linear-gradient(135deg, #000000 0%, #7f8c8d 74%);
  background-image: -o-linear-gradient(135deg, #000000 0%, #7f8c8d 74%);
  background-image: linear-gradient(315deg, #000000 0%, #7f8c8d 74%);
}

p {
  text-align: center;
}



